@import url(https://fonts.googleapis.com/css2?family=Livvic:wght@300&family=Martel:wght@300;400&display=swap);
@font-face {
  font-family: "Phos";
  src: url("https://files.jcink.net/uploads/noatest001/Phoss___.ttf")
    format("TrueType");
}
@font-face {
  font-family: "Aqua";
  src: url("https://files.jcink.net/uploads/novacodes/aqua.ttf");
}

::-webkit-scrollbar {
  width: 10px;
}
::-webkit-scrollbar-track {
  background: #1b1b1b;
}
::-webkit-scrollbar-thumb {
  background: var(--color);
}

body {
  background: #1b1b1b;
  background-size: cover;
  background-attachment: fixed;
  font: 11px/100% "Calibri", sans-serif;
  color: #cdcdcd;
  margin: 0;
  padding: 0;
  --color: #4d6d9a;
  --color3: rgba(77, 109, 154, 0.3);
  --color5: rgba(77, 109, 154, 0.5);
  --color7: rgba(77, 109, 154, 0.7);
  --black3: rgba(0, 0, 0, 0.3);
  --black4: rgba(0, 0, 0, 0.4);
  --black5: rgba(0, 0, 0, 0.5);
  --black7: rgba(0, 0, 0, 0.7);
  --light3: rgba(239, 239, 239, 0.3);
  --light5: rgba(239, 239, 239, 0.5);
  --light7: rgba(239, 239, 239, 0.7);
  --dark: #1b1b1b;
  --dark0: rgba(27, 27, 27, 0);
  --dark3: rgba(27, 27, 27, 0.3);
  --dark5: rgba(27, 27, 27, 0.5);
  --dark7: rgba(27, 27, 27, 0.7);
}

a:link,
a:visited,
a:active {
  text-decoration: none;
  color: var(--color);
}

.wrapper {
  width: 100%;
  padding: 0 0 1px 0;
  position: relative;
  top: 100vh;
  left: 0;
  z-index: 98;
  box-sizing: border-box;
  background: #1b1b1b url(/static/media/bg.adf1284d.jpg);
  background-size: cover;
  background-attachment: fixed;
  background-position: 0 0;
  background-position: initial;
}

.innerwrapper {
  width: calc(100% - 200px);
  margin: 100px auto;
  padding: 100px 0;
  background: var(--black4);
  outline: 1px solid var(--light3);
}

h1 {
  text-align: center;
  padding: 0;
  font: 40px/100% "Aqua", serif;
  color: var(--color);
  text-transform: uppercase;
}

h2 {
  text-align: center;
  font: 40px/100% "Martel", serif;
  color: var(--light7);
  text-transform: uppercase;
}

h3 {
  text-align: center;
  font: 11px/100% "Livvic", sans-serif;
  text-transform: uppercase;
  letter-spacing: 2px;
  color: var(--color7);
}

.catwrap {
  width: calc(100% - 220px);
  margin: 0 auto 100px;
  position: relative;
  outline: 1px solid var(--black7);
  background: var(--black3);
  display: flex;
  flex-flow: row wrap;
  align-items: flex-start;
  justify-content: space-between;
}

.catwrap:before {
  content: "";
  border-left: 1px solid var(--light3);
  border-top: 1px solid var(--light3);
  display: flex;
  padding: 5px;
  position: absolute;
  top: -10px;
  left: -10px;
  width: 50px;
  height: 50px;
}

.catwrap:after {
  content: "";
  border-right: 1px solid var(--light3);
  border-bottom: 1px solid var(--light3);
  display: flex;
  padding: 5px;
  position: absolute;
  bottom: -10px;
  right: -10px;
  width: 50px;
  height: 50px;
}

.catwrap:last-child {
  margin-bottom: 0;
}

@media only screen and (min-device-width: 320px) and (max-device-width: 820px) {
  .innerwrapper {
    width: 95%;
    margin: 20px;
    padding: 20px;
    box-sizing: border-box;
  }

  .catwrap {
    box-sizing: border-box;
    width: 100%;
    padding: 10px; 
    border: 1px solid var(--color3);
  }

  .catwrap:before,
  .catwrap:after {
    display: none;
  }
}
html.sr .load-hidden {
  visibility: hidden;
}

header {
  width: 100%;
  height: 100vh;
  box-sizing: border-box;
  position: fixed;
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 9;
  background: linear-gradient(to top, var(--black7), var(--color3)),
    url(/static/media/stars.e17a41b7.jpg);
  background-size: cover;
  background-position: center;
  background-attachment: fixed;
}

header > big {
  margin: 0;
  position: absolute;
  font: 5rem/200% "Aqua", serif;
  color: var(--light7);
  letter-spacing: 50px;
  text-transform: uppercase;
  text-shadow: 2px 2px 20px rgba(63, 63, 63, 0.7);
  mix-blend-mode: screen;
}

header > big span {
  color: var(--color7);
  animation: flicker 4s infinite;
}

header > big span:nth-of-type(1) {
  animation-delay: 1s;
}

header > big span:nth-of-type(3) {
  animation-delay: 1.7s;
}

header > big:before {
  content: "";
  border-left: 1px solid var(--light5);
  border-top: 1px solid var(--light5);
  background: transparent;
  display: flex;
  width: 50px;
  height: 50px;
  position: absolute;
  left: -40px;
  top: -5px;
}

header > big:after {
  content: "";
  border-right: 1px solid var(--light5);
  border-bottom: 1px solid var(--light5);
  background: transparent;
  display: flex;
  width: 50px;
  height: 50px;
  position: absolute;
  bottom: -10px;
  right: -10px;
}

@keyframes flicker {
  0% {
    opacity: 1;
  }
  31.8% {
    opacity: 0.9;
  }
  32% {
    opacity: 0;
  }
  32.2% {
    opacity: 0.9;
  }
  94.6% {
    opacity: 1;
  }
  95% {
    opacity: 0;
  }
  95.4% {
    opacity: 0.9;
  }
  100% {
    opacity: 1;
  }
}

@media only screen and (min-device-width: 320px) and (max-device-width: 820px) {
  header {
    height: 100vh;
  }

  header > big {
    font: 3rem/200% "Aqua", serif;
    margin: 20px auto;
    letter-spacing: 10px;
  }

  header > big:before,
  header > big:after {
    height: 20px;
    width: 20px;
  }

  header > big:before {
    left: -20px;
  }
}

footer {
	margin-top: 100px; 
}

/* social media links */
.links-con {
    width: 100%;
    box-sizing: border-box;
    padding: 15px 0;
    min-height: 30px;
    background: var(--color5);
    margin: 5px 0 0 0;
    display: flex;
    flex-wrap: wrap; 
    align-items: center;
    justify-content: space-evenly;
    align-content: space-evenly;
    text-align: center; 
    outline: 1px solid var(--color7);
}

/* link ul li styling for social media links */
.links-con .he-links {
	list-style-type: none;
	display: inline-flex;
	justify-content: space-evenly;
	align-items: center;
	width: 100%;
	height: 100%;
	margin: 0px;
}

.links-con .he-links .he-li {
	width: 20px;
	height: 20px;
	padding: 20px !important;
	transition: all 0.5s ease;
	margin: 0;
}

.links-con .he-links .he-li a {
	font-size: 30px;
	text-transform: uppercase;
    color: var(--light7);
    text-shadow: -2px -2px 10px var(--light5);
}
#toplinks {
  width: 100%;
  min-height: 60px;
  box-sizing: border-box;
  background: linear-gradient(to left, var(--color3), var(--color7)),
    url(/static/media/bg.adf1284d.jpg);
  background-size: cover;
  background-attachment: fixed;
  background-position: inherit;
  position: -webkit-sticky;
  position: sticky;
  top: 0px;
  z-index: 99;
  border-top: 1px solid black;
  border-bottom: 1px solid black;
}

#toplinks .buttons {
  width: 85%;
  height: 60px;
  margin: 0 auto;
  display: flex;
  flex-flow: row wrap;
  align-items: center;
  justify-content: space-evenly;
  align-content: center;
}

#toplinks .buttons a {
  font: 10.3px/100% "Livvic", sans-serif;
  font-weight: 300;
  color: rgba(239, 239, 239, 0.95);
  text-transform: uppercase;
  letter-spacing: 1px;
  text-shadow: 2px 2px 10px var(--light5);
  display: inline-block;
  position: relative;
  padding: 0;
}

#toplinks .buttons a:before,
#toplinks .buttons a:after {
  content: "";
  display: flex;
  position: absolute;
  width: 10px;
  height: 10px;
  background: transparent;
  transform: scaleX(0) scaleY(0);
  transition: all 0.3s linear;
}

#toplinks .buttons a:before {
  border-left: 1px solid var(--light5);
  border-top: 1px solid var(--light5);
  top: -10px;
  left: -20px;
  transform-origin: 0% 0%;
}

#toplinks .buttons a:after {
  border-right: 1px solid var(--light5);
  border-bottom: 1px solid var(--light5);
  bottom: -10px;
  right: -20px;
  transform-origin: 100% 100%;
}

#toplinks .buttons a:hover:before,
#toplinks .buttons a:hover:after {
  transform: scaleX(1) scaleY(1);
}

@media only screen and (min-device-width: 320px) and (max-device-width: 820px) {
  #toplinks .buttons a:before,
  #toplinks .buttons a:after {
    width: 8px;
    height: 8px;
  }

  #toplinks .buttons a:before {
    top: -7px;
    left: -9px;
  }

  #toplinks .buttons a:after {
    bottom: -8px;
    right: -8px;
  }
}

