.maintitle {
  align-self: stretch;
  -ms-writing-mode: tb-rl;
      writing-mode: vertical-rl;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  box-sizing: border-box;
  padding: 30px 5px;
  text-align: right;
  letter-spacing: 3px;
  word-spacing: 6px;
  position: relative;
  background: linear-gradient(to top, var(--dark3), var(--color7));
  border-right: 1px solid var(--black7);
}

.maintitle a {
  font: 20px/130% "Martel", serif;
  text-transform: uppercase;
  color: var(--light7);
  text-shadow: -2px -2px 10px var(--light5);
  transform: rotate(180deg);
  position: -webkit-sticky;
  position: sticky;
  top: 50px;
  margin-left: 30px;
  padding-left: 30px;
}

.maintitle a:hover {
  color: var(--light);
}

.wrap-flex-col img {
